body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Alerts {
    position: fixed;
    right: 40px;
    top: 60px;
    z-index: 1000;
}
.Drop {
    float: left;
    padding: 0 10px 20px 10px;
    width: 12.5%;
}

.Drop.ignored {
    display: none;
}

.Drop.ignored.showFilter {
    display: block;
}

@media (min-width: 2400px) {
    .Drop.columns_auto { width: calc(100% / 12); }
}

@media (min-width: 1200px) and (max-width: 2399px) {
    .Drop.columns_auto { width: calc(100% / 8); }
}

@media (min-width: 980px) and (max-width: 1199px) {
    .Drop.columns_auto { width: calc(100% / 5); }
}

@media (min-width: 768px) and (max-width: 979px) {
    .Drop.columns_auto { width: calc(100% / 4); }
}

@media (min-width: 480px) and (max-width: 767px) {
    .Drop.columns_auto { width: calc(100% / 3); }
}

@media (max-width: 479px) {
    .Drop.columns_auto { width: calc(100% / 2); }
}

.Drop.columns_2 { width: calc(100% / 2); }
.Drop.columns_4 { width: calc(100% / 4); }
.Drop.columns_6 { width: calc(100% / 6); }
.Drop.columns_8 { width: calc(100% / 8); }
.Drop.columns_12 { width: calc(100% / 12); }
.Drop.columns_16 { width: calc(100% / 16); }

.Drop .DropBorder {
    background-color: #FFFFFF;
    border: 1px solid black;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    width: 100%;
    -webkit-transition: background-color 0.2s ease-out;
    transition: background-color 0.2s ease-out;
}

.Drop.active .DropBorder {
    background-color: #CCE5FF;
    -webkit-transition: background-color 0s ease;
    transition: background-color 0s ease;
}

.Drop.DropBonus .DropBorder {
    background-color: #f8d7da;
}

.Drop.DropBonus.active .DropBorder {
    background-color: #FFFFFF;
}

.Drop .DropContent {
    padding: 10px;
}

.Drop .DropIconBox {
    margin-bottom: 10px;
    padding-top: 110%;
    position: relative;
    width: 100%;
}

.Drop .DropIcon {
    cursor: pointer;
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.Drop .DropIndicator {
    font-size: 18px;
    font-weight: bold;
    left: 10px;
    pointer-events: none;
    position: absolute;
    text-shadow: -1px 0 #FFF, 0 1px #FFF, 1px 0 #FFF, 0 -1px #FFF;
    top: 10px;
}

.Drop .DropQuantity {
    bottom: 10px;
    font-size: 16px;
    font-weight: bold;
    pointer-events: none;
    position: absolute;
    right: 10px;
    text-shadow: -1px 0 #FFF, 0 1px #FFF, 1px 0 #FFF, 0 -1px #FFF;
}

.Drop .DropInput {
    margin-bottom: 10px;
    text-align: center;
}

.Drop .DropToggle {
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    display: none;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
}

.Drop.showFilter .DropToggle {
    display: block;
}

.Drop .DropToggle .DropToggleIcon {
    color: #FFFFFF;
    font-size: 60px;
    height: 60px;
    margin: auto;
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
}
.Node .DropsContainer {
    margin: 20px -10px 0 -10px;
    overflow: auto;
    zoom: 1;
}
.Event {
    margin-bottom: 20px;
}

.Event .NodeSelector {
    display: block;
    margin-bottom: 20px;
}

.App {
    margin: 0 auto;
    padding: 10px 20px;
    user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
}

.App.width_480 { width: 480px; }
.App.width_720 { width: 720px; }
.App.width_1080 { width: 1080px; }
.App.width_2160 { width: 2160px; }

.App .EventSelector {
    display: block;
    margin-bottom: 20px;
}

.App .AppTitle {
    position: relative;
}

.App .AppTitle>h1>small {
    font-size: 16px;
}

.App .AppSettingsToggle {
    bottom: 5px;
    position: absolute;
    right: 0;
}

.App .AppLinks {
    bottom: 5px;
    font-size: 24px;
    position: absolute;
    right: 50px;
}

.App .AppLinks a {
    margin: 0 5px;
}

.App .AppLoading {
    background-color: rgba(0, 0, 0, 0.5);
    color: #FFFFFF;
    font-size: 200px;
    height: 100%;
    left: 0;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
}

.App .AppLoading .AppLoadingContent {
    position:absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    height: 200px;
    line-height: 200px;
}

