.App {
    margin: 0 auto;
    padding: 10px 20px;
    user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
}

.App.width_480 { width: 480px; }
.App.width_720 { width: 720px; }
.App.width_1080 { width: 1080px; }
.App.width_2160 { width: 2160px; }

.App .EventSelector {
    display: block;
    margin-bottom: 20px;
}

.App .AppTitle {
    position: relative;
}

.App .AppTitle>h1>small {
    font-size: 16px;
}

.App .AppSettingsToggle {
    bottom: 5px;
    position: absolute;
    right: 0;
}

.App .AppLinks {
    bottom: 5px;
    font-size: 24px;
    position: absolute;
    right: 50px;
}

.App .AppLinks a {
    margin: 0 5px;
}

.App .AppLoading {
    background-color: rgba(0, 0, 0, 0.5);
    color: #FFFFFF;
    font-size: 200px;
    height: 100%;
    left: 0;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
}

.App .AppLoading .AppLoadingContent {
    position:absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    height: 200px;
    line-height: 200px;
}
